@import "./Variables.scss";

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: larger;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: $font1;
  color: $c1;
  float: right;
  margin: 0;
  padding-right: 20px;
  padding-top: 15px;
  font-size: inherit;
}

.headingCont {
  width: 100%;
  background-color: $c3;
  background-image: linear-gradient(270deg, $c3 0%, $c6 45%, $c6 100%);
  height: 60px;
}

.headingCont h1 {
  padding-top: 15px;
}

.clearfloat {
  font-size: 0px;
  margin: 0px;
  padding: 0px;
  clear: both;
  float: none;
  height: 0px;
  width: 0px;
  border: 0px none;
  line-height: 0px;
}

.hiddentext {
  position: absolute;
  right: -200vw;
  top: -200vw;
  width: 0px;
  height: 0px;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.load {
  height: 55px;
  position: absolute; 
  bottom: 20px;
  right: 40px;
}

main {
  max-width: 1920px;
  margin: auto;
  position: relative;
  min-height: 89.5vh;
  display: flex;
  flex-direction: column;
}

@media only screen and (max-width: 1200px) {
  #di {
    padding-top: 0 !important;
  }
}

@media only screen and (min-width: 1921px) {
  main {
    box-shadow: 1px 0 rgba($c4, 0.2), -1px 0 rgba($c4, 0.2);
  }
}