@import "../Variables.scss";

/* silde */

.slick-slide img {
  max-width: 1200px;
  height: 100%;
  min-height: 130px;
  width: 100%;
  margin: auto;
}

/* welcome */

.welcome {
  position: absolute;
  margin: 2% 0 0 calc(52% - 600px);
  z-index: 997;
  font-size: x-large;
  @media only screen and (max-width: 1200px) {
    margin: 2% 0 0 4%;
    font-size: medium;
  }
  @media only screen and (max-width: 640px) {
    font-size: small;
  }
  #welcomeOne {
    font-family: $font3;
    margin: 0;
    padding: 2%;
    font-weight: 500;
    color: $c3;
    background-color: $c9;
    border-radius: 5px;
    animation: slide1;
    animation-duration: 2s;
    animation-fill-mode: forwards;
    line-height: 35px;
    width: fit-content;
    float: none;
    #my-svg {
      stroke: $c3;
      stroke-width: 13;
      fill: none;
      vertical-align: bottom;
      overflow: visible;
    }
  }
  #welcomeTwo {
    background-color: $c9;
    border-radius: 5px;
    padding: 5%;
    animation: slide2;
    animation-duration: 2s;
    animation-fill-mode: forwards;
    margin: 6% auto auto 4%;
    font-weight: 500;
    font-size: inherit;
    width: min-content;
    min-width: 178px;
    color: $c3;
    &:hover {
      text-decoration: underline;
      animation: slide2;
      animation-duration: 1.5s;
      animation-fill-mode: forwards;
    }
  }
}

/* vid */

.flip {
  animation: flip 0.5s ease-in-out;
}

.video {
  text-align: center;
  margin: auto;
  width: 100%;
  position: relative;
  top: -105px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  @media only screen and (max-width: 1200px) {
    top: -90px;
    width: 90%;
  }
  @media only screen and (max-width: 640px) {
    top: -4px;
    padding: 5% 0;
    background-color: $c12;
    width: 80%;
  }
  .videoImg {
    margin: 0 2.5% 0 2.5%;
    border-radius: 3.75px;
    box-shadow: 0 0 1.5px $c1;
    width: 275px;
    height: inherit;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    @media only screen and (max-width: 1200px) {
      width: 35%;
    }
    @media only screen and (max-width: 640px) {
      width: 39%;
      border-radius: 2.5px;
    }
  }
  .videoLink {
    background-color: $c9;
    position: absolute;
    font-family: $font3;
    color: $c3;
    width: 275px;
    top: 10.3%;
    margin-left: 2.5%;
    cursor: pointer;
    font-style: italic;
    padding: 5px 0;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    @media only screen and (max-width: 1200px) {
      width: 35%;
    }
    @media only screen and (max-width: 640px) {
      width: 39%;
      border-top-left-radius: 2.5px;
      border-top-right-radius: 2.5px;
      font-size: 90%;
    }

    &:hover {
      color: $c7;
    }
  }
  #left {
    left: calc(52.5% - 600px);
    @media only screen and (max-width: 1200px) {
      left: 1%;
    }
    @media only screen and (max-width: 640px) {
      left: -6%;
    }
    &:hover {
      fill: $c7;
    }
  }
  #right {
    right: calc(52.5% - 600px);
    @media only screen and (max-width: 1200px) {
      right: 1%;
    }
    @media only screen and (max-width: 640px) {
      right: -6%;
    }
    &:hover {
      fill: $c7;
    }
  }
  #left,
  #right {
    position: absolute;
    top: 65%;
    color: $c4;
    opacity: 0.75;
    fill: $c4;
    @media only screen and (max-width: 640px) {
      top: 40%;
    }
  }
  svg {
    vertical-align: sub;
    font-size: 1.25em;
  }
}

/* info */

.info {
  height: 500px;
  box-shadow: 0 0 7.5px $c2;
  @media only screen and (max-width: 992px) {
    display: flex;
    flex-direction: column-reverse;
    height: unset;
  }
  .infoOne {
    height: 100%;
    width: 400px;
    float: left;
    background-image: url("https://candid.s3-ap-southeast-2.amazonaws.com/info1.png");
    background-position: center;
    background-size: 100% 100%;
    @media only screen and (max-width: 992px) {
      float: none;
      margin: 0;
      height: 200px;
      width: 100%;
      background-image: url("https://candid.s3-ap-southeast-2.amazonaws.com/info.png");
      background-position: center top;
      background-size: 150px 150px;
      background-repeat: no-repeat;
    }
  }
  .infoTwo {
    width: 55%;
    color: $c3;
    float: left;
    margin-top: 4%;
    margin-left: 7.5%;
    @media only screen and (max-width: 1200px) {
      width: 46%;
      margin-left: 9%;
    }
    @media only screen and (max-width: 992px) {
      float: none;
      margin: 0;
      padding: 5%;
      width: 90%;
    }
    svg {
      color: $c2;
      margin: 0 10px;
    }
    h3 {
      width: 100%;
      text-align: right;
      font-family: $font2;
      color: $c3;
      font-weight: bold;
      margin: 0 0 5% 0;
      padding: 0;
    }
    p {
      color: $c1;
      background-color: $c2;
      border-radius: 100px;
      padding: 30px;
      text-align: center;
      font-style: oblique;
      font-family: $font4;
      @media only screen and (max-width: 992px) {
        border-radius: 2.5px;
        padding: 20px;
      }
    }
  }
}

/* card */

.card {
  max-width: 1312px;
  padding: 125px 2%;
  z-index: 998;
  clear: both;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  overflow: hidden;
  margin: auto;
  span img {
    margin-left: 10px;
    vertical-align: middle;
  }
  p {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 15px;
    text-align: center;
    font-family: $font3;
    font-weight: 500;
    text-shadow: 0 0 0.5px $c9;
    border-radius: 2.5px;
    box-shadow: 0 0 5px $c3;
  }
  h3 {
    margin: 0;
    padding: 0;
    font-size: inherit;
    font-family: $font2;
    font-weight: 600;
    display: inline;
  }
  .cardOne {
    max-width: 339px;
    margin: auto;
    color: $c3;
    padding: 15px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    a {
      color: $c2;
      float: left;
      margin-left: 30px;
      width: 30px;
      font-weight: 900;
      font-size: 15px;
      &:hover {
        color: $c8;
      }
    }
    svg:nth-child(3) {
      color: $c2;
      float: left;
      margin-left: 30px;
    }
    svg:nth-child(4) {
      color: $c3;
      font-size: 50px;
      align-self: center;
    }
  }
  .cardTwo {
    align-self: flex-end;
  }
}

/* inq */

.enquiry {
  background-color: $c5;
  margin-top: 10px;
  h3 {
    font-family: $font1;
    color: $c1;
    float: right;
    margin: 0;
    padding-right: 20px;
    padding-top: 15px;
    padding-top: 15px;
  }
  .MuiFormControl-root-www {
    width: 20%;
    @media only screen and (max-width: 1200px) {
      width: 98%;
    }
  }
  .react-select__menu-list,
  .MuiFilledInput-multiline {
    scrollbar-face-color: $c3;
    scrollbar-track-color: $c1;
    overflow-y: auto;
    overflow-x: hidden;
    scrollbar-color: $c3 $c1;
    &::-webkit-scrollbar {
      width: 10px;
    }
    &::-webkit-scrollbar-thumb {
      background: $c3;
    }
    &::-webkit-scrollbar-track {
      background: $c1;
    }
    &::-webkit-scrollbar-thumb:hover {
      background: $c7;
    }
  }
}

@keyframes slide1 {
  0% {
    transform: translate(-10px, -5px);
  }
  100% {
    transform: translate(0, 0);
  }
}

@keyframes slide2 {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(2.5px, 2.5px);
  }
  100% {
    transform: translate(0, 0);
  }
}

@keyframes flip {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.99);
  }
  100% {
    transform: scale(1);
  }
}
