@import "../Variables.scss";

.center {
  text-align: center;
}

.centerBlock {
  display: block;
  margin: auto;
}

.justify {
  text-align: justify;
}

#readMoreZero,
#readMoreOne {
  display: none;
}

.grid {
  &:nth-child(1) {
    width: 36%;
    padding: 0 4% 0 4%;
    @media only screen and (max-width: 640px) {
      width: 100%;
      padding: 0;
      p {
        width: calc(92% - 24px);
        margin: auto;
      }
    }
  }
  &:nth-child(2) {
    width: 52%;
    padding-right: 4%;
    @media only screen and (max-width: 640px) {
      width: 92%;
      padding: 0;
    }
    div {
      border-radius: 10px;
      @media only screen and (max-width: 640px) {
        border-radius: 0;
      }
    }
  }
}

.aboutContOne {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 60px 0;
  h2 {
    font-family: $font2;
    color: $c3;
    margin-left: 10px;
    font-size: inherit;
  }
  p {
    color: $c1;
    background-color: $c2;
    border: 2px solid $c2;
    border-radius: 2.5px;
    font-family: $font1;
    word-wrap: break-word;
    padding: 10px;
    @media only screen and (max-width: 1200px) {
      border-radius: 0;
    }
  }
  svg {
    vertical-align: sub;
    float: right;
    margin-right: 25px;
  }
}

.aboutContTwo {
  margin: 20px auto auto auto;
  width: 100%;
  max-width: 1200px;
  h2 {
    font-family: $font2;
    font-size: inherit;
    color: $c3;
    margin-left: 10px;
    margin: auto;
    width: calc(100% - 10px);
  }
  h3 {
    font-family: $font2;
    color: $c1;
    text-align: right;
    font-size: 90%;
    padding-right: 7.5%;
  }
  h3 + span {
    width: 71%;
    margin: auto;
    display: block;
  }
  span {
    display: inline-block;
  }
  img {
    width: 30%;
    display: block;
    margin: 8% auto;
    @media only screen and (max-width: 1200px) {
      width: 60%;
    }
  }
  #pic {
    background-image: url("https://candid.s3-ap-southeast-2.amazonaws.com/about.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    float: left;
    width: 450px;
    height: 300px;
    border: 1px solid $c1;
    border-top-right-radius: 25%;
    border-bottom-right-radius: 25%;
    border-top-left-radius: 17%;
    border-bottom-left-radius: 17%;
    filter: grayscale(100%);
    @media only screen and (max-width: 1365px) {
      border: unset;
      border-bottom: 2px solid $c1;
      float: none;
      display: block;
      margin: 20px auto 90px auto;
      border-radius: 0;
      width: 25%;
      height: unset;
      padding: 25%;
    }
  }
  #picSpan {
    width: 53%;
    padding-left: 50px;
    margin-top: 100px;
    text-align: right;
    height: 120px;
    @media only screen and (max-width: 1365px) {
      padding-left: 0;
      width: 100%;
      margin: auto;
      height: unset;
    }
  }
  button {
    background-color: transparent;
    outline: none;
    padding: 20px;
    font-size: 80%;
    color: $c1;
    border: 1px solid $c1;
    border-radius: 100px;
    display: block;
    margin: 2% auto;
    cursor: pointer;
    &.btn {
      margin-right: 4%;
      @media only screen and (max-width: 1365px) {
        margin-right: auto;
      }
    }
  }
  svg {
    float: right;
    color: $c2;
    margin-right: 25px;
  }
}
